.btn-yellow {
  background: #D2B16E;
  border-radius: 0.625rem;
  span {
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 600;
    color: #111;
  }
  &:hover {
    background: #F0D582;
  }
  &:active {
    background: #916B3E;
  }
}
h2 {
  font-size: 2.25rem;
  line-height: 130%;
  font-weight: 900;
}
.form-group {
  border: none;
  padding: 0;
  margin: 0;
  input {
    width: 100%;
    height: 3.625rem;
    background: #EDECEC;
    border-color: transparent;
    border-radius: 0.375rem;
    padding: 0 1.5rem;
    font-size: 1rem;
    line-height: 130%;
    color: #000000;
    &::placeholder {
      color: #898484;
    }
  }
  textarea {
    width: 100%;
    height: 8.75rem;
    background: #EDECEC;
    border-color: transparent;
    border-radius: 0.375rem;
    padding: 1.25rem 1.5rem;
    font-size: 1rem;
    line-height: 130%;
    color: #000000;
    &::placeholder {
      color: #898484;
    }
  }
}
.agreement-check {
  position: relative;
  input {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
  }
  input:checked ~ label {
    &:after {
      background-image: url("../img/check.svg");
    }
  }
  label {
    font-size: 0.8125rem;
    line-height: 130%;
    padding-left: 2.5rem;
    a {
      position: relative;
      z-index: 2;
    }
    a:hover {
      color: #F0D683;
    }
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 1.875rem;
      height: 1.875rem;
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
      border: 0.0625rem solid #CA9D49;
      border-radius: 0.5625rem;
      transition: all 0.3s;
    }
  }
}
#modal-not-agreement {
  color: #000000;
}
.button {
  display: flex;
  justify-content: center;
}
.navigation {
  display: grid;
  grid-template-columns: 100%;
  gap: 1.5rem;
  padding: 5rem 0;
  a {
    font-size: 1.5rem;
    line-height: 130%;
    color: #000000;
    &:hover {
      color: #9F734C;
    }
  }
}
.left-anim {
  transform: translate(-60%, 0);
  opacity: 0;
  transition: all 0.4s;
}
.left-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.right-anim {
  transform: translate(60%, 0);
  opacity: 0;
  transition: all 0.4s;
}
.right-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
@keyframes img {
  0% {
    transform: translate(0);
  }
  50% {
    transform: translate(-25%, 0);
  }
  100% {
    transform: translate(0);
  }
}


@media screen and (max-width: 1399px) {
  h2 {
    font-size: 2rem;
  }
}

@media screen and (max-width: 576px) {
  h2 {
    font-size: 1.75rem;
  }
}