html {
  font-size: max(calc(16px * var(--coef_h)), 15px);
}
body {
  font-family: Inter, sans-serif;
  background: linear-gradient(180deg, #111 0%, #221B1B 70.27%, #131212 97.92%, #111 100%);
  color: #ffffff;
}
.fixed-body {
  overflow: hidden;
}
.wrapper {
  position: relative;
  overflow: hidden;
}
.container-fluid {
  max-width: 1800px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Inter;
  margin: 0;
}
.btn {
  display: flex !important;
  justify-content: center;
  align-items: center;
  span {
    min-width: auto;
  }
}
img {
  object-fit: cover;
}