.footer {
  position: relative;
  z-index: 1;
  background: #111;
  padding: 6.25rem 0;
  margin-top: -32.5rem;
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
  }
  &__left {
    max-width: 17.5rem;
    p {
      font-size: 1.25rem;
      line-height: 130%;
      font-weight: 700;
      margin: 3rem 0 0 0;
    }
  }
  &__right {
    max-width: 74.375rem;
    width: 100%;
  }
  h2 {
    .text_color {
      color: #F0D683;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 3.75rem / 3));
    gap: 1.875rem;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background: linear-gradient(180deg, #272727 0%, #171717 100%);
    border: 0.0625rem solid #585858;
    border-radius: 0.3125rem;
    padding: 2rem;
    &-title {
      font-size: 1.5rem;
      line-height: 130%;
      font-weight: 700;
      color: #F0D683;
    }
    .download {
      width: 2.25rem;
      margin-top: 1.5rem;
      svg {
        width: 100%;
        height: auto;
      }
    }
    &-img {
      max-width: 15.625rem;
      margin-top: 0.5rem;
    }
    &-address {
      display: grid;
      grid-template-columns: 100%;
      gap: 0.75rem;
      text-align: center;
      margin-top: 1.5rem;
      p {
        font-size: 1.25rem;
        line-height: 130%;
        margin: 0;
      }
    }
    &-socials {
      display: flex;
      align-items: center;
      gap: 0.75rem;
      margin-top: 1.5rem;
    }
    &-social {
      width: 2.5rem;
      height: 2.5rem;
      display: flex;
      justify-content: center;
      align-items: center;
      background: radial-gradient(119.38% 119.38% at 10.4% 8.42%, #3D3D3D 0%, #141414 100%);
      border-radius: 0.625rem;
      padding: 0.16875000000000004rem;
      img {
        width: 100%;
        height: 100%;
      }

      &:hover {
        background: #3D3D3D;
      }
    }
    &-phones {
      display: grid;
      grid-template-columns: 100%;
      gap: 0.75rem;
      margin-top: 1.5rem;
    }
    &-phone {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      gap: 0.3125rem;
      span {
        font-size: 1.125rem;
        line-height: 130%;
      }
      a {
        font-size: 1.25rem;
        line-height: 130%;
        font-weight: 700;
      }
      a:hover {
        color: #F0D683;
      }
    }
  }
}

@media screen and (max-width: 1500px) {
  .footer {
    &__item {
      padding: 2rem 1.5rem;
    }
  }
}

@media screen and (max-width: 1399px) {
  .footer {
    padding: 5rem 0;
    &__left {
      max-width: 15rem;
      p {
        font-size: 1.125rem;
      }
    }
    &__items {
      grid-template-columns: repeat(3, calc(100% / 3 - 3rem / 3));
      gap: 1.5rem;
    }
    &__item {
      &-title {
        font-size: 1.25rem;
      }
      &-address {
        p {
          font-size: 1.125rem;
        }
      }
      &-phone {
        span {
          font-size: 1rem;
        }
        a {
          font-size: 1.125rem;
        }
      }
    }
  }
}

@media screen and (max-width: 1023px) {
  .footer {
    padding: 3.125rem 0;
    &__inner {
      display: block;
    }
    &__left {
      max-width: 100%;
      p {
        margin-top: 2rem;
      }
    }
    &__right {
      max-width: 100%;
      margin-top: 2rem;
    }
    &__items {
      grid-template-columns: repeat(3, calc(100% / 3 - 2rem / 3));
      gap: 1rem;
    }
    &__item {
      padding: 1.5rem 1rem;
      &-title {
        font-size: 1.125rem;
      }
      &-address {
        p {
          font-size: 1rem;
        }
      }
      &-phone {
        span {
          font-size: 0.875rem;
        }
        a {
          font-size: 1rem;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 1rem / 2));
    }
  }
}

@media screen and (max-width: 499px) {
  .footer {
    &__items {
      grid-template-columns: 100%;
    }
  }
}