#header {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  color: #FFF;
  padding: 3.125rem 0 0 0;
  .header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.25rem;
  }
  .header__right {
    display: flex;
    align-items: center;
    gap: 2.5rem;
  }
  .header__logo {
    width: 16.5625rem;
    display: block;
    img {
      width: 100%;
    }
  }
  .header__locations {
    position: relative;
    display: flex;
    align-items: center;
  }
  .header__location {
    position: relative;
    font-size: 14px;
    line-height: 130%;
    &:hover {
      color: #F0D683;
    }
  }
  .header__location:nth-child(1) {
    padding-right: 1.25rem;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 0.0625rem;
      height: 2.5rem;
      background: #979797;
      border-radius: 0.625rem;
    }
  }
  .header__location:nth-child(2) {
    padding-left: 1.25rem;
  }
  .header__socials {
    display: flex;
    align-items: center;
    gap: 1.25rem;
  }
  .header__social {
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: radial-gradient(119.38% 119.38% at 10.4% 8.42%, #3D3D3D 0%, #141414 100%);
    border-radius: 0.625rem;
    padding: 0.16875000000000004rem;
    img {
      width: 100%;
      height: 100%;
    }
    &:hover {
      background: #3D3D3D;
    }
  }
  .header__group {
    display: flex;
    flex-direction: column;
  }
  .header__phone {
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    color: #FFF;
    &:hover {
      color: #F0D683;
    }
  }
  .header__btn {
    position: relative;
    justify-content: flex-end;
    background: transparent;
    padding: 0;
    margin-top: 0.5rem;
    span {
      font-size: 1.25rem;
      line-height: 130%;
      color: #F0D683;
      padding: 0;
      border-bottom: 0.0625rem dashed #F0D683;
      padding-bottom: 0.3125rem;
    }
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 0.5rem;
      height: 0.5rem;
      background: #F0D683;
      border-radius: 50%;
    }
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: -0.3125rem;
      transform: translate(0, -50%);
      width: 1.125rem;
      height: 1.125rem;
      background: #F0D683;
      border-radius: 50%;
      filter: blur(0.75rem);
    }
    &:hover {
      span {
        color: #ffffff;
        border-color: #ffffff;
      }
      &:after,
      &:before {
        background: #ffffff;
      }
    }
  }
}
.burger {
  position: relative;
  z-index: 12;
  width: 2rem;
  height: 1.25rem;
  cursor: pointer;
  display: none;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 0.125rem;
    background: #F0D683;
    border-radius: 0.1875rem;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 0.5625rem;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 0.5625rem;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1300px) {
  #header {
    .header__right {
      gap: 1.25rem;
    }
    .header__location:nth-child(1) {
      padding-right: 0.625rem;
    }
    .header__location:nth-child(2) {
      padding-left: 0.625rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  #header {
    .header__locations {
      flex-direction: column;
      gap: 0.625rem;
    }
    .header__location:nth-child(1) {
      padding-right: 0;
      &:after {
        display: none;
      }
    }
    .header__location:nth-child(2) {
      padding-left: 0;
    }
  }
}

@media screen and (max-width: 1023px) {
  #header {
    padding-top: 2rem;
    .header__left {
      position: relative;
      z-index: 6;
    }
    .header__right {
      position: fixed;
      z-index: 5;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      flex-direction: column;
      background: #010E0F;
      padding: 9.375rem 2rem 5rem;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      &--active {
        opacity: 1;
        pointer-events: all;
      }
    }
    .header__logo {
      width: 12rem;
    }
  }
  .burger {
    display: block;
  }
}