.main {
  background-image: url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    padding: 15.9375rem 0 6.25rem 0;
    &:after {
      content: '';
      position: absolute;
      z-index: 1;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 1250rem;
      height: 100%;
      background: linear-gradient(180deg, rgba(27, 22, 22, 0.00) 73.68%, rgba(25, 21, 21, 0.63) 79.51%, rgba(23, 20, 20, 0.89) 88.88%, rgba(21, 19, 19, 0.98) 93.42%, #131212 100%, rgba(19, 18, 18, 0.56) 99.05%);
      pointer-events: none;
    }
  }
  &__left {
    position: relative;
    z-index: 2;
    max-width: 43.125rem;
  }
  &__group {
    position: relative;
    padding: 3.75rem 0 3.75rem 2.5rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 21.5625rem;
      height: 100%;
      border: 0.4375rem solid #F0D683;
      border-right: unset;
      filter: drop-shadow(0px 4px 24.2px rgba(236, 209, 128, 0.50));
      pointer-events: none;
    }
  }
  h1 {
    font-size: 3.5rem;
    line-height: 130%;
    font-weight: 900;
    color: #F0D683;
    margin: 0;
    .text_color {
      color: #ffffff;
    }
  }
  p {
    max-width: 35rem;
    font-size: 1.75rem;
    line-height: 130%;
    color: #ffffff;
    margin: 2rem 0 0 0;
    .text_color {
      color: #F0D683;
    }
  }
  .lines {
    span:nth-child(1) {
      position: absolute;
      top: 0;
      left: 21.5625rem;
      width: 0.4375rem;
      height: 3.125rem;
      background: #F0D683;
      filter: drop-shadow(0px 4px 24.2px rgba(236, 209, 128, 0.50));
      pointer-events: none;
    }
    span:nth-child(2) {
      position: absolute;
      left: 21.5625rem;
      bottom: 0;
      width: 0.4375rem;
      height: 3.125rem;
      background: #F0D683;
      filter: drop-shadow(0px 4px 24.2px rgba(236, 209, 128, 0.50));
      pointer-events: none;
    }
  }
  &__info {
    max-width: 28.4375rem;
    font-size: 1rem;
    line-height: 130%;
    color: #ffffff;
    margin-top: 2.8125rem;
    .text_color {
      color: #F0D683;
    }
  }
  &__btn {
    width: 13.75rem;
    margin-top: 1.5rem;
  }
  &__img {
    position: absolute;
    right: -3.125rem;
    bottom: 0;
    max-width: 75rem;
    width: 100%;
  }
}
.info {
  padding: 6.25rem 0 2.5rem;
  &:after {
    content: '';
    position: absolute;
    top: -12.5rem;
    left: 0;
    width: 100%;
    height: 80%;
    background-image: url("../img/info-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    pointer-events: none;
  }
  &:before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    transform: rotate(-180deg);
    width: 100%;
    height: 40%;
    background: linear-gradient(180deg, rgba(27, 22, 22, 0.00) 73.68%, rgba(25, 21, 21, 0.63) 79.51%, rgba(23, 20, 20, 0.89) 88.88%, rgba(21, 19, 19, 0.98) 93.42%, #131212 100%, rgba(19, 18, 18, 0.56) 99.05%);
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__top {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    gap: 2rem;
  }
  &__left {
    position: relative;
    max-width: 38.125rem;
    p {
      max-width: 25rem;
      font-size: 1.5rem;
      line-height: 130%;
      font-weight: 200;
      margin: 2rem 0 0 0;
      strong {
        font-weight: 700;
      }
    }
  }
  &__right {
    max-width: 26.875rem;
  }
  h2 {
    .text_color {
      color: #F0D683;
    }
  }
  &__quote {
    position: relative;
    font-size: 1.5rem;
    line-height: 130%;
    strong {
      font-weight: 700;
    }
    strong:nth-child(2) {
      display: block;
      text-align: right;
      margin-top: 1rem;
    }
    i {
      position: absolute;
      top: calc(100% - 2rem);
      left: 1.25rem;
      width: 3.625rem;
      img {
        width: 100%;
      }
    }
  }
  .quotation-marks {
    position: absolute;
    top: -9rem;
    left: 0;
    font-size: 28.75rem;
    line-height: 90%;
    color: #3B3B3B;
    opacity: 0.5;
    pointer-events: none;
  }
  &__img {
    max-width: 22.5rem;
    margin-left: auto;
    img {
      width: 100%;
    }
  }
  &__center {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
  }
  &__img2 {
    position: relative;
    max-width: 25rem;
    width: 100%;
    margin: 0 auto;
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 37.5rem;
      height: 37.5rem;
      border: 0.0625rem solid rgba(226, 197, 121, 0.50);
      border-radius: 50%;
      pointer-events: none;
    }
    img {
      position: relative;
      z-index: 1;
      width: 100%;
    }
  }
  &__group {
    position: absolute;
    z-index: 1;
    left: -13.5rem;
    bottom: 7.5rem;
    max-width: 18rem;
    strong {
      font-size: 1.25rem;
      line-height: 130%;
      font-weight: 900;
    }
    p {
      font-size: 1.125rem;
      line-height: 130%;
      font-weight: 300;
      color: #D7D7D7;
      margin: 1rem 0 0 0;
    }
  }
  &__phone {
    width: 100%;
    height: 3.375rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(240, 214, 131, 0.35);
    backdrop-filter: blur(0.125rem);
    border-radius: 0.4375rem;
    padding: 0 1rem;
    margin-top: 2rem;
    i {
      flex-shrink: 0;
      width: 1.875rem;
      display: flex;
      align-items: center;
      margin-right: 0.25rem;
    }
    i svg {
      width: 100%;
      height: auto;
    }
    span {
      font-size: 1.25rem;
      line-height: 130%;
      font-weight: 900;
    }
    &:hover {
      background: rgba(240, 214, 131, 0.6);
    }
  }
  &__bottom {
    position: relative;
    margin-top: 11.25rem;
  }
  &__slider-top {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 6.25rem;
  }
  &__slider-nav {
    position: relative;
    display: flex;
    align-items: center;
    gap: 1.5rem;
  }
  .cases {
    position: absolute;
    left: 2.5rem;
    bottom: calc(100% + 0.625rem);
    display: flex;
    flex-direction: column;
    align-items: center;
    span {
      font-size: 1.25rem;
      line-height: 130%;
      font-weight: 700;
      color: #F0D683;
    }
    i {
      width: 3rem;
    }
    i img {
      width: 100%;
    }
  }
  .slick-arrow{
    position: static;
    width: 3.4375rem;
    height: 3.4375rem;
    border: 0.0625rem solid #9F734C;
    border-radius: 0.1875rem;
    &:hover {
      background: #3D3D3D;
    }
    svg {
      width: 2rem;
      height: auto;
    }
  }
  .slides-numbers {
    font-size: 2.5rem;
    line-height: 100%;
    letter-spacing: -0.4px;
    .total {
      font-size: 1.5rem;
    }
  }
  &__slider {
    position: relative;
    z-index: 1;
    margin-top: 3.125rem;
  }
  .slick-list {
    margin: 0 -1.25rem;
  }
  .slick-track {
    display: flex;
    align-items: stretch;
  }
  .slick-slide {
    height: auto;
    margin: 0 1.25rem;
    & > div {
      height: 100%;
    }
  }
  &__group2 {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 1.5rem;
    margin-top: 3.125rem;
    p {
      font-size: 1rem;
      line-height: 130%;
      margin: 0;
      span {
        color: #D2B16E;
      }
    }
  }
  &__btn {
    width: 21.5625rem;
  }
  &__img3 {
    position: absolute;
    z-index: 1;
    top: -3.125rem;
    left: 0;
    max-width: 48.125rem;
    width: 100%;
    pointer-events: none;
  }
  &__img4 {
    position: absolute;
    top: -10rem;
    left: 0;
    width: 15rem;
  }
  &__img5 {
    position: absolute;
    left: 0;
    bottom: 12.5rem;
    width: 27.5rem;
  }
}
.info-card {
  position: relative;
  height: 100%;
  display: flex !important;
  flex-direction: column;
  border-radius: 0.1875rem;
  padding: 0 2rem 3.8125rem 2rem;
  &:after {
    content: '';
    position: absolute;
    left: 0.4375rem;
    bottom: 0.4375rem;
