    position: relative;
    z-index: 1;
    max-width: 24.375rem;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    margin: 0.625rem auto 0;
  }
  &__city {
    text-align: right;
    font-size: 1rem;
    line-height: 130%;
  }
  &__title {
    font-size: 1.5rem;
