    color: #F0D683;
    margin-top: 1rem;
  }
  &__text {
    font-size: 1.125rem;
    line-height: 130%;
    margin-top: 2rem;
    margin-bottom: auto;
  }
  &__btn {
    width: 14.0625rem;
    margin: 4rem auto 0;
  }
}
