@media screen and (max-width: 399px) {
  .info {
    &__btn {
      width: 100%;
    }
  }
  .channels {
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .professionals {
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .connection {
    &__items {
      grid-template-columns: 100%;
      margin-top: 2rem;
    }
    .professional-card:nth-child(2) {
      margin: 0 !important;
    }
  }
}